/* Tpl: Home page */

#home {
    height: 100vh;
    position: relative;
    top: 0;
    align-items: stretch;
    font-weight: 300;
    color: #777;
    background: linear-gradient(320deg, #f5f5f5, #efefef); }

.home__right {
    width: 100%; }

#home,
.home__right {
    display: flex; }

.home__right-content {
    margin: auto;
    max-width: calc(100% - 40px); }

.home__right-content {
    width: 400px; }

.slogan {
    font-size: 14px;
    text-align: center;
    position: absolute;
    top: 30px;
    left: 15px; }

.slogan span {
    font-size: 26px;
    font-weight: 900; }

.servis-promo {
    padding: 0 30px; }

.home-left__networks {
    position: absolute;
    top: -100px;
    left: 0px;
    padding: 0 20px;
    font-size: 16px;
    z-index: 2; }

.home-left__networks .title {
    font-size: 12px;
    padding-left: 10px;
    color: #aaa; }


.home-right__title {
    margin-bottom: 20px; }

.home__right input {
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    margin-bottom: 20px; }


#login-button {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #777;
    cursor: pointer;
    font-size: 1.5em;
    &.active {
        box-shadow: inset 0px -2px 5px white, inset 0px 2px 5px rgba(0, 0, 0, 0.15); } }

#home footer {
    position: absolute;
    height: 50px;
    top: calc(100vh - 50px);
    left: 0;
    right: 0;
    width: 100%;
    padding: 20px;
    border-top: 1px solid #e3e3e3;
    z-index: 1;
    background: linear-gradient(320deg, #efefef, #e7e7e7); }

#home footer ul li {
    padding: 10px 0;
    font-size: 10pt;
    list-style: none; }

@media (max-width: 768px) {
    #home footer {
        height: auto;
        top: 100vh; }
    .home-left__networks {
        width: 100%; } }
