.social-buttons{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .social{
    width: 46px;
    height: 46px;
    border-radius: 50%;
    font-size: 1em;
    margin: 10px;
    cursor: pointer;
    background-color: #f2f2f2;
    position: relative;
    transition: all .3s linear;
  }
  
  .social a{
    color: #777;
  }