.top-bar, .top-bar-optional {
    position: fixed;
    z-index: 10;
    bottom: 0;
    width: 100%;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    border-top: 1px solid #e3e3e3;
    background: linear-gradient(to top, #fff, #f7f7f7);
 }    //filter: drop-shadow(0px -2px 0px #555)
.top-bar-optional {
    z-index: 11; }

.top-bar__item {
    border: 0;
    padding: 0 5px;
    cursor: pointer;
    &::after {
        content: '';
        width: 80px;
        height: 80px;
        border-radius: 50%;
        background-color: #fefefe;
        position: absolute;
        top: -10px;
        z-index: -1; }
    & span {
        text-transform: uppercase;
        font-size: 10px;
        font-weight: 700;
        color: #777; }
    button {
        border-radius: 50%;
        width: 35px;
        height: 35px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 0;
        font-size: 20px; } }

.minicart-widget__info {
    display: flex;
    flex-direction: column;
    font-size: 12px; }

.minicart-widget__info>div {
    display: flex;
    justify-content: space-between; }

.minicart-widget__info>div b {
    margin-left: 10px; }

#foto-groups {
    padding: 20px; }

.foto-group {
    &:nth-child(1) {
        .foto-group__title {
            margin-top: 0; } } }


.foto-group__title {
    font-size: 20px;
    text-transform: uppercase;
    font-weight: bold;
    border-bottom: 1px solid #ddd;
    margin: 40px 0 20px 0; }

.foto-group__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }

.foto-group__img {
    width: calc(20% - 20px);
    max-width: 280px;
    overflow: hidden;
    margin: 0 0 10px 0;
    position: relative;
    &.current-item {
        width: calc(100% - 6px);
        max-width: 350px;
        margin-bottom: 40px;
        box-shadow: none;
        & .foto-group__img-wrapper {
            padding: 0; }
        & img {
            position: relative;
            border: 10px solid transparent; }
        & [data-current="true"] {
            border: 10px solid #df691a; } }
    &.horz {
        margin: 0 auto 20px auto;
        .foto-group__img-wrapper {
            padding-top: 150px; } } }

.foto-group__img:hover button {
    right: 20px;
    bottom: 20px;
    opacity: 1; }

.foto-group__img:hover .zoom-foto {
    left: 20px;
    cursor: default; }

.foto-group__img-wrapper {
    width: 100%;
    padding-top: 100%;
    background-color: transparent; }

.foto-group__img img {
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    transition: all .3s; }

.zoomed.foto-group__img {
    overflow: visible;
    z-index: 8; }

.zoomed {
    .foto-group__img {
        img {
            width: 150%; } }
    &.horz .foto-group__img {
        img {
            height: 100%; } } }

.zoomed.foto-group__img .open-foto-options-btn {
    opacity: 0; }

.zoomed.foto-group__img .zoom-foto {
    opacity: 0.3; }

.foto-group__img .open-foto-options-btn,
.foto-group__img .zoom-foto {
    position: absolute;
    right: -10px;
    bottom: 3px;
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    color: #555;
    font-size: 18px;
    background-color: #f2f2f2;
    line-height: 0;
    z-index: 9;
    transition: all .3s;
    opacity: 0;
    border: 1px solid #ccc;
    border-radius: 50%; }

.zoom-foto {
    left: -10px;
    right: auto; }

[data-stacked="1"] {
    box-shadow: 1px -1px 3px #000, 3px -3px 0px 1px #ddd, 4px -4px 3px #000, 6px -6px 0px 1px #aaaaaa;
    border-radius: 0 5px 0 0; }

.change-foto-icon {
    position: absolute;
    border: 2px solid #fff;
    border-radius: 50%;
    font-size: 20pt;
    width: 44px;
    text-align: center;
    right: 10px;
    bottom: 10px; }

.img-wrapper {
    position: relative; }
.img-mask {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
    background-color: rgba(255,255,255, 0.3); }
.formats-wrapper {
    width: 310px;
    margin: auto; }
#formats {
    position: fixed;
    right: 0;
    top: 0;
    left: 0;
    bottom: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    z-index: 12;
    background-color: rgba(0,0,0,0.5); }
.formats__item-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between; }
.formats__item {
    padding: 15px;
    background: #fefefe;
    border: 3px solid #ddd;
    border-radius: 10px;
    color: #777;
    margin-bottom: 10px;
    cursor: pointer;
    text-align: center;
    width: 210px;
    margin: 5px auto;
    justify-content: space-between;
    &.cancel {
        background-color: #df691a;
        color: #ffffff;
        text-transform: uppercase;
        font-weight: bold; }
    &.item-total {
        width: 270px;
        padding: 5px 0;
        background-color: #5bc0de;
        color: #ffffff;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 24px;
        margin-bottom: 20px;
        overflow: hidden; }
    .item-total__cart-icon {
        background-color: #df691a;
        padding: 10px;
        margin: -8px 0;
        border-radius: 10px;
        border: 3px solid #dddddd;
        border-right: 0;
        min-width: 88px;
        max-width: 88px;
        animation: slideInToLeft 0.3s ease-in; }
    .item-total__wrapper {
        width: 150px;
        margin: auto;
        &>div {
            align-items: baseline; } } }
.qty_mod {
    width: 44px;
    height: 44px;
    border: 3px solid #dddddd;
    background-color: #fff;
    color: #ffffff;
    border-radius: 10px;
    font-size: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
    &--add {
        background-color: #5cb85c; }
    &--remove {
        background-color: #d9534f; } }
.formats__title {
    text-transform: uppercase;
    margin-right: 10px; }
.formats__cost {
    display: block;
    font-size: 20px;
    font-weight: 700; }

@media (min-width: 1400px) {
    .foto-group__img {
        width: calc(100%/6 - 20px); } }

@media (max-width: 430px) {
    .foto-group__img {
        width: calc(50% - 10px);
        &.horz {
            width: calc(100% - 10px);
            max-width: 100%; } } }

// ================================= CART
#minicart-widget {
    .badge {
        color: #fff;
        position: absolute;
        top: 20px;
        right: 20px;
        font-weight: lighter; } }
#cart-widget-open {
    &.layer {
        background-color: #2b3e50; } }
.cart-item {
    &__wrapper {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        margin: 5px;
        padding: 5px;
        position: relative;
        background-color: #f3f3f3; }
    &__img {
        position: relative;
        width: 50%;
        padding-top: 50%;
        overflow: hidden;
        img {
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
            border: 1px solid #ddd; } }
    &__info {
        width: 50%;
        padding-left: 15px;
        display: flex;
        flex-direction: column;
        font-size: 130%;
        font-weight: bold;
        color: #555555;
        span {
            margin-bottom: 5px; }
        small {
            width: 45px;
            display: inline-block; } }
    &__controlls {
        width: 100%;
        margin-top: 10px; } }

//================================ CHECKOUT
#checkout {
    font-size: 18px;
    label {
        font-size: 14px; }
    .item-total__wrapper {
        font-size: 26px;
        width: 250px;
        margin: -16px auto 30px auto;
        border: 2px solid #949494;
        border-radius: 3px;
        background-color: #0567a0;
        color: #fff;
        padding: 10px 30px;
        align-items: center;
        div {
            width: 150px; } }
    h5 {
        padding-bottom: 2px;
        font-size: 16px;
        margin-bottom: 15px; }
    p {
        color: #ffffff;
        text-align: center; } }
