
// Sidebar CSS

#account-wrapper {
    display: flex; }

#sidebar {
    width: 72px;
    min-height: 100vh;
    border-right: 2px solid #202e3b;
    background-color: #4e5d6c!important;
    .badge__wrapper {
        position: absolute;
        bottom: -1px;
        width: 100%;
        text-align: center; }
    .list-group-item {
        width: 70px;
        height: 70px;
        padding: 0;
        display: flex;
        position: relative;
        span {
            margin: auto; }
        svg {
            margin-bottom: 3px;
            filter: drop-shadow(1px 1px #555); }
        strong {
            font-size: 10px;
            text-transform: uppercase;
            letter-spacing: 1px;
            text-shadow: 1px 1px #555; } } }
.sidebar__footer {
    padding: 0 10px; }
.sidebar__header,
.main__header {
    height: 40px;
    display: flex;
    align-items: center;
    padding: 0 15px; }
.sidebar__header {
    background-color: #2b3e50!important; }
#logo {
    font-size: 20px;
    font-weight: 700; }
.mainmenu {
    padding: 0;
    margin: 0; }
.mainmenu li {
    cursor: pointer;
    transition: all .3s; }

//==================== MAIN
#main {
    width: 100%;
    min-height: 100vh; }
.main__body {
    padding: 30px; }
.table td, .table th {
    text-align: center;
    vertical-align: middle; }

.icon, .action-icon {
    cursor: pointer; }
.action-icon {
    padding: 10px;
    border: 1px solid #aaa; }

//============================== DRIVE
.breadcrumbs {
    margin-bottom: 20px; }
.drive__dir {
    .src-icon {
        font-size: 24px;
        margin-right: 10px; }
    .title {
        margin: 0 10px; } }

//============================== SINGLE ORDER
#single-order {
    strong {
        font-size: 110%; } }
.single-order__title {
    border-bottom: 1px solid #ddd;
    margin-bottom: 30px; }
.single-order__items {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    .cart-item__wrapper {
        width: calc(25% - 10px); }
    .cart-item__info {
        font-size: 100%; }
    .cart-item__title {
        color: #333;
        width: 100%;
        text-align: center; } }

//======================= FORMATS LIST
.formats-list__wrapper {
    display: flex;
    flex-wrap: wrap;
    background-color: #fff;
    color: #333;
    .form-check {
        width: calc(50% - 10px);
        margin: 5px; } }


//======================= Util
.display-flex {
    display: flex; }
.justify-between {
    justify-content: space-between; }

.mt2 {
    margin-top: 20px; }
.mt3 {
    margin-top: 30px; }
.mb1 {
    margin-bottom: 10px; }
.mb2 {
    margin-bottom: 20px; }
.mr1 {
    margin-right: 10px; }
.mr2 {
    margin-right: 20px; }
.mla {
    margin-left: auto; }
.mra {
    margin-right: auto; }

.modal.open {
    display: block;
    overflow: auto; }

small {
    font-size: 10px!important; }

.alert {
    position: fixed;
    bottom: 5px;
    right: 5%; }

.list-group {
    li {
        svg {
            color: #5bc0de;
            font-size: 20px; }
        &[data-current=true] {
            background-color: #5bc0de;
            svg {
                color: #fff; } } } }

i[data-tip] {
    border-bottom: 1px dotted; }

.fons80 {
    font-size: 80%; }
.fons120 {
    font-size: 120%; }

.input-group-text {
    padding: .1rem .75rem; }

#sidebar .toast {
    margin-top: 100px; }
