/* Default App styles */

@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;700&display=swap');

body {
  font-family: 'Rubik', sans-serif;
  font-size: 14px; }
*:focus {
  outline: none; }

.shadow-up, .shadow-up-hover {
  box-shadow: 0px -6px 10px rgba(255, 255, 255, 1), 0px 4px 15px rgba(0, 0, 0, 0.15);
  background-color: #f2f2f2;
  border: 0;
  transition: all .3s linear;
  position: relative;
  color: #777; }

.shadow-up-hover:hover {
  box-shadow: 0 15px 20px rgba(0, 0, 0, 0.02); }


.shadow-down {
  box-shadow: 0 15px 20px rgba(0, 0, 0, 0.02);
  border: 0; }

.shadow-down:after,
.shadow-up-hover:hover:after {
  content: "";
  box-shadow: inset 0px -2px 5px white, inset 0px 2px 5px rgba(0, 0, 0, 0.15);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  z-index: -1; }


.shadow-up-hover:hover:after {
  z-index: 1; }


footer, .top-bar {
  padding: 10px 20px;
  font-size: 12px;
  display: flex;
  justify-content: space-between;
  width: inherit;
  color: #999; }

#page-title {
  font-size: 20px;
  color: #333; }

#page-title span:nth-child(1) {
  font-size: 24px;
  font-weight: bold;
  color: #1A81BB; }


/* COLORS */
.blue {
  color: #1A81BB; }

/* NOTICE */
.linear-notice,
.linear-notice--error {
  font-size: 12px;
  padding: 10px;
  text-align: center;
  background-color: #fff;
  border: 1px solid #ddd; }
.linear-notice--error {
  border-left: 5px solid red; }
.bar__notice {
  font-size: 16px;
  text-align: right;
  padding: 5px 15px;
  font-weight: bold;
  width: 200px;
  min-height: 60px;
  font-style: italic;
  background-color: #5bc0de;
  color: #ffffff;
  margin: -15px -15px -7px 0;
  position: relative;
  animation: slideInToTop 0.3s ease-in;
  &:after {
    content: '';
    position: absolute;
    left: -21px;
    top: -23px;
    bottom: 0;
    border: 21px solid transparent;
    border-right: 0;
    border-bottom: 58px solid #5bc0de; } }

/* UTILITES */
.inline-tips-starter {
  font-size: 40px;
  color: #f0ad4e;
  position: absolute;
  bottom: 10px;
  left: 100px;
  cursor: pointer; }
.inline-tip {
  max-width: 900px; }
.alert-inline {
  position: inherit!important; }
.animation-rotate {
    animation: rotate 1s infinite linear; }
.pointer {
  cursor: pointer; }
.layer {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: auto; }

