/* FLEX */
.d-block {
  display: block; }
.flex,
.flex-center,
.flex-column {
  display: flex; }

.flex-center {
  align-items: center; }
.justify-between {
  justify-content: space-between; }
.justify-center {
  justify-content: center; }

@media (max-width: 768px) {
  .flex-column-md {
    flex-direction: column; } }


@keyframes slideInToTop {
  0% {
    margin-bottom: -100px; }
  100% {
    margin-bottom: -15px; } }
@keyframes slideInToLeft {
  0% {
    margin-right: -100px; }
  100% {
    margin-right: -8px; } }

@keyframes rotate {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

